*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #000000;
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;

}
.calc{
  background-color: transparent;
  padding: 20px;
  border-radius: 20px;
  box-shadow: -1px -1px 10px 10px rgba(189,97, 97, 0.5);
  border: none;
}
#input{
  width: 100%;
  max-width: 400px;
  border-radius: 20px;
  height: 100px;
  border: none;
  background: transparent;
  text-align: center;
  color: #fff;
  cursor: pointer;

}
button{
  border: none;
  color: #fff;
  margin: 10px;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  background: transparent;
  cursor: pointer;
  box-shadow: 6px 6px 10px rgba(190, 97, 97, 1),
  -6px -6px 10px rgba(190,97, 97, 1)
  
}